export const Discard = (G, ctx, id) => {
  const idx = G.players[ctx.currentPlayer].hand.findIndex((ac) => ac.id === id)
  const removed = G.players[ctx.currentPlayer].hand.splice(idx, 1)
  G.discardDeckOnBoard.push(removed[0])
  ctx.events.endStage()
}
